
.main[data-v-67717268] {
    display: grid;
    grid-template-columns: 1fr;
    width: calc(100vw - (100vw - 100%));
    margin: 0 auto;
    text-align:center;
}
.a-section[data-v-67717268] {
    margin: 0 auto;
    width: 100%;
    padding-bottom: 75px;
    background-color: hsl(218, 34%, 20%);
    background-image: url(assets/images/376a8b95194fb33e0a203bf52f8cb467.svg);
    background-position: bottom center;
    background-repeat: no-repeat;
    background-size:contain;
}
.a-section__img[data-v-67717268] {
    max-width: 550px;
    min-width: 300px;
    width: 75%;
    height: auto;
}
.a-section__article[data-v-67717268] {
    max-width: 400px;
    margin: 0 auto;
    margin-bottom: 25px;
    margin-top: 30px;
}
.b-section[data-v-67717268] {
    gap: 40px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    margin-top: 100px;
    margin-bottom: 100px;
}
.b-section__icon-grid[data-v-67717268] {
    margin: 0 auto;
    padding: 20px;
    max-width: 300px;
}
.icon-grid__img[data-v-67717268] {
    height: 75px;
    width: auto;
}
.c-section[data-v-67717268] {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    gap: 1vw;
    align-items:center;
    justify-items: end;
    margin: 0 auto;
    margin-bottom: 95px;
    margin-top: 25px;
}
.c-section__img[data-v-67717268] {
    max-width: 550px;
    min-width: 210px;
    width: 75%;
    height: auto;
}
.c-section__article[data-v-67717268] {
    margin: 0 auto;
    max-width: 375px;
    text-align:left;
    width: 75%;
}
.c-section__button[data-v-67717268] {
    margin-top: 10px;
}
.d-section[data-v-67717268] {
    width: 60vw;
    display: grid;
    grid-template-columns: 1fr;
    margin: 0 auto;
    margin-bottom: 175px;
    margin-top: 75px;
}
.d-section__card[data-v-67717268] {
    padding-bottom: 15px;
    margin: 0 auto;
    background-color: hsl(218, 34%, 20%);
    margin: 15px;
    -webkit-box-shadow: 2px 2px 5px -1px rgba(0,0,0,0.56);
    -moz-box-shadow: 2px 2px 5px -1px rgba(0,0,0,0.56);
    box-shadow: 2px 2px 5px -1px rgba(0,0,0,0.56);
    color: hsl(0, 0%, 100%);
}
.d-section__img[data-v-67717268] {
    position:absolute;
    width: 35px;
    height:auto;
}
.d-section__intro[data-v-67717268] {
    font-style:italic;
    margin: 0 auto;
    width: 85%;
    margin-bottom: 15px;
    margin-top: 15px;
}
.d-section__article[data-v-67717268] {
    display: grid;
    grid-template-columns: 1fr 2fr;
    width: 75%;
    justify-items: center;
    align-items: center;
    margin: 0 auto;
}
.d-section__title[data-v-67717268]{
    margin: 0;
    padding: 0;
}
.d-section__article-image[data-v-67717268] {
    width: 50px;
    height: 50px;
    float:left;
    border-radius: 50%;
}
.e-section[data-v-67717268] {
    width: 50vw;
    height: auto;
    margin: 0 auto;
    padding: 35px;
    z-index:1;
    border-radius: 10px;
    background-color: hsl(217, 28%, 15%);
    -webkit-box-shadow: 2px 2px 5px -1px rgba(0,0,0,0.56);
    -moz-box-shadow: 2px 2px 5px -1px rgba(0,0,0,0.56);
    box-shadow: 2px 2px 5px -1px rgba(0,0,0,0.56)
}
.e-section__title[data-v-67717268] {
    margin-top: 0px;
}
.e-section__paragraph[data-v-67717268] {
    width: 60%;
    margin: 0 auto;
    padding: 5px 0px 30px 0px;
}
.e-section__form[data-v-67717268] {
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;;
    gap: 15px;
    margin: 0 auto;
}
.form__input[data-v-67717268] {
    width: 100%;
    border-radius: 13px;
    height: 43px;
    border: 0px;
    text-align:center;
