/* only for smartphones */
@media screen and (max-width: 480px) {
.main[data-v-67717268] {
        width: 100vw;
}
.b-section[data-v-67717268] {
        grid-template-columns: 1fr;
}
.c-section[data-v-67717268] {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
        justify-items: center;
}
.c-section__article[data-v-67717268] {
        text-align:center;
}
.d-section[data-v-67717268] {
        width: 90vw;
}
.e-section[data-v-67717268] {
        width: 70vw;
}
.e-section__paragraph[data-v-67717268] {
        width: 90%;
}
}

/* for wide resolution screens */
@media screen and (min-width: 850px) {
.d-section[data-v-67717268] {
        width: 85vw;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
}
.e-section__input[data-v-67717268] {
        grid-template-columns: 2fr 1fr;
        gap: 15px;
        width: 70%;
}
.e-section__input[data-v-67717268]:nth-last-child(1) {
        justify-items:start;
}
}
