
.main-nav[data-v-30300f61] {
    display: grid;
    grid-template-columns: 1fr 10fr;
    padding: 6vh 4vw 6vh 5vw;
    background-color: hsl(218, 34%, 20%);
}
.main-nav__logo[data-v-30300f61] {
    width: 100%;
    min-width: 60px;
    height: auto;
    cursor: pointer;
}
.main-nav__menu[data-v-30300f61] {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.main-nav__menu-item[data-v-30300f61] {
    display: inline;
    margin: 0px 25px 0px 0px;
}
.main-nav__menu-item-link[data-v-30300f61] {
    text-decoration: none;
}

/* only for smartphones */
@media screen and (max-width: 480px) {
.main-nav__logo[data-v-30300f61] {
        min-width: 35px;
        height: auto;
        margin-left: 5vw;
}
.main-nav[data-v-30300f61] {
        grid-template-columns: 1fr 4fr;
        margin: 6vh 4vw 6vh 2vw;
}
.main-nav__menu-item[data-v-30300f61] {
        margin: 0px 20px 0px 0px;
}
}
